import { OPERATORS } from './operator.const'

/**
 * Get operator icon
 * @param operator - the operator
 * @returns the operator icon if present
 */
export function getOperatorIcon(
  operatorCode: string,
  square?: boolean
): string | undefined {
  const operator = OPERATORS.find((o) => o.code === operatorCode)
  return square ? operator?.logoSquare : operator?.logo
}
